/** @jsxImportSource @emotion/react */
import React from 'react'
import { ICellRendererParams } from 'ag-grid-community'
import { ReviewableSample } from './PendingBoxReview'
import MuiIconButton from '@mui/material/IconButton'
import EditIcon from '@mui/icons-material/Edit'
import { withTooltip } from '../withTooltip'

interface CellRendererParams<T> extends ICellRendererParams {
  data: T
}

export const ActionsRenderer = ({ data }: CellRendererParams<ReviewableSample>): React.ReactNode => {
  const { onEdit } = data
  return (
    <div css={{ display: 'flex', alignItems: 'center', height: '100%' }}>
      <MuiIconButton onClick={onEdit}>{withTooltip(<EditIcon />, 'Edit sample match')}</MuiIconButton>
    </div>
  )
}
